import React from 'react';
import './stylesheet.css';
import PartnerLogo1 from './images/section_images/partner_logo_1.png';
import { db, auth } from './firebase';
import MenuBox from './menuBox';
import Restaurants from './restaurantBox';
import { useState } from 'react';
import { useEffect } from 'react';

const MenuItem = (props) => {
    const [state, setState] = useState({
        category: null,
    });

    const getCategory = async () => {
        console.log(props);
        console.log('mounted');
        const lengthOfCategory = await db
            .collection('restaurants')
            .doc(props.id.id)
            .collection('categories')
            .get();
        const category = [];
        const data = await db
            .collection('restaurants')
            .doc(props.id.id)
            .collection('categories')
            .doc(props.id.categoryid)
            .get();

        console.log(data.data());

        setState({ category: data.data() });
    };
    useEffect(() => {
        getCategory();
    }, [props]);

    return (
        <div class='menu-items-grid'>
            {state.category !== null ? (
                state.category.items ? (
                    state.category.items.map((item, index) => {
                        console.log(item);
                        return (
                            <a key={index}>
                                <div class='menu-item'>
                                    <div class='menu-item-info'>
                                        <div class='menu-item-top'>
                                            <h3>{item.name}</h3>
                                            <p>${item.price}</p>
                                        </div>
                                        <p>{item.description}</p>
                                    </div>
                                </div>
                            </a>
                        );
                    })
                ) : state.category.categories ? (
                    state.category.categories.map((cat, i) =>
                        cat.items
                            ? cat.items.map((val, l) => (
                                <a key={i}>
                                    <div class='menu-item'>
                                        <div class='menu-item-info'>
                                            <div class='menu-item-top'>
                                                <h3>{val.name}</h3>
                                                <p>${val.price}</p>
                                            </div>
                                            <p>{val.description}</p>
                                        </div>
                                    </div>
                                </a>
                            ))
                            : null
                    )
                ) : null
            ) : (
                    <div>No Items</div>
                )}
        </div>
    );
};

export default MenuItem;
